<template>
  <ul>
    <li v-for="song of songs" :key="song.id" class="list-item">
      <img :src="song.pictureUrl" alt="" class="list-item__img" />
      <router-link :to="{ name: 'Song', params: { songId: song.id } }" target="_blank">{{
        song.name
      }}</router-link>
    </li>
  </ul>
</template>

<script>
export default {
  name: 'Section',
  props: {
    sectionName: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      songs: [],
    };
  },
  async mounted() {
    await this.loadSongOptions();
  },
  methods: {
    async loadSongOptions() {
      const response = await fetch(`${process.env.VUE_APP_API_BASE_URL}/content/songs.json`);
      const allSongs = await response.json();

      this.songs = allSongs.filter((song) => song.sectionName === this.sectionName);
    },
  },
};
</script>

<style scoped>
.list-item__img {
  width: 100px;
}
</style>
